.about-container {
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    padding-bottom: 10rem;
    width: 90%;
    margin: 0px auto;
}

.about-us {
    padding-top: 80px;
    color: var(--blue);
}

section {
    margin: 20px 0;
}

section h1 {
    font-size: 2em;
    color: var(--orange);
    text-align: center;
    margin-bottom: 20px;
}

.about-us .rotate {
    background-color: rgba(0, 0, 0,0.1);
    padding: 20px;
    box-shadow: 1px 1px 4px 0px rgb(66, 66, 66);
    backdrop-filter: blur(5px);
    background-clip: padding-box;
    transform: rotateZ(3deg);
    border-radius: 20px;
}

.about-us .rotate div {
    transform: rotateZ(-3deg);
}



.curriculum div{
    color: var(--blue);
    background-color: rgba(0, 0, 0,0.1);
    padding: 20px;
    backdrop-filter: blur(5px);
    box-shadow: 1px 1px 4px 0px rgb(66, 66, 66);
    background-position: 50% 50%;
    border-radius: 20px;
}

.faculty div{
    color: var(--blue);
    background-color: rgba(0, 0, 0,0.1);
    backdrop-filter: blur(5px);
    background-clip: padding-box;
    padding: 20px;
    box-shadow: 1px 1px 4px rgb(66, 66, 66);
    border-radius: 20px;
}

#about-icon-container{
    display: flex;
    justify-content: space-evenly;
    font-size: 2.5em;
}

section h4 {
    color: var(--blue);
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 20px;
}

.about-splash {
    position: absolute;
    height: 400px;
    width: 400px;
    top: 200px;
}

@media (min-width:600px){
    .rotate {
        width: 75%;
        margin: 40px auto;
    }

    .curriculum {
        width: 75%;
        margin: 80px auto;
    }

    .faculty {
        width: 75%;
        margin: 80px auto;
    }

    .about-container {
        font-size: 1.5em;
    }
    .about-splash {
        position: absolute;
        height: 500px;
        width: 500px;
        top: 200px;
    }
   
}

@media (min-width:1100px){
    .rotate {
        width: 60%;
        margin: 0 auto;
    }

    .curriculum {
        width: 60%;
        margin: 40px auto;
    }

    .faculty {
        width: 60%;
        margin: 40px auto;
    }

    .about-container {
        font-size: 2em;
        padding-top: 1rem;
    }
    .about-splash {
        position: absolute;
        height: 800px;
        width: 800px;
        top: 200px;
    }
}

@media (min-width:1800px){
    .about-splash {
        position: absolute;
        height: 800px;
        width: 800px;
        top: 200px;
    }
    
    .rotate {
        width: 50%;
        margin: 0 auto;
    }

    .curriculum {
        width: 50%;
        margin: 40px auto;
    }

    .faculty {
        width: 50%;
        margin: 40px auto;
    }

    .about-container {
        font-size: 2em;
        /* padding-top: 1rem; */
    }
    .about-container .rotate div{
       
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }

    .faculty div{
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }
    .curriculum div{
        font-size: 1.2em;
        font-weight: bold;
        padding: 40px;
    }
   
}

