.funding-container{
    height: calc(100vh - 6rem);
    padding-top: 6rem;
    width: 100%;
    font-size: 1.5em;
    perspective: 1000px;
    overflow: hidden;
    padding-bottom: 7rem;
}

.funding-container h1{
    text-align: center;
    color: var(--orange);
}


#finding-splash{
    position: absolute;
    top: 140px;
    width: 200px;
    height: 350px;
    z-index: -1;
  }

  .funding-section {
      color: var(--blue);
      transform-style: preserve-3d;
      padding: 10px;
      margin: 0 auto;
      width: 80%;
      position: relative;
      bottom: 150px;
      font-size: 1.2em;
      background: rgba(0, 0, 0, 0.1);
      backdrop-filter: blur(30px);
      border-radius: 20px;
      background-clip: padding-box;
      border: 3px solid transparent;
      box-shadow: 10px 10px 10px rgba(255, 255, 255, 0.1);
  }

  .spacer {
      padding-bottom: 1rem;
  }

  strong {
      /* font-weight: bolder; */
      background-color: var(--orange);
      color: white;
      padding: 2px;
      border-radius: 5px;
  }

  .paintmark{
     display: none;
  }

  @media (min-width:600px){
      .funding-container {
          font-size: 2em;
      }

      #funding-splash svg{
          transform: scale(2) translate(2rem, 1rem);
      }
  }

  @media (min-width:900px){
      .funding-container {
          font-size: 2.5em;
      }

      #funding-splash svg{
          transform: scale(2.5) translate(2rem, 1rem);
      }
  }

  @media (min-width:900px){
      .funding-container {
          padding-top: 6rem;
      }
  }

  @media (min-width:1100px){
      .paintmark {
          display: inline-block;
          position: absolute;
          top: 20rem;
          right: 10px;
          opacity: 0.7;
          z-index: -1;
      }
  }

  @media (min-width:1400px){
      .funding-container {
          font-size: 3em;
      }
      #funding-splash svg{
        transform: scale(4) translate(2.6rem, 2rem);
    }
  }

  @media (min-width:1700px){
      .funding-container {
          font-size: 3em;
          overflow: hidden;
      }
      #funding-splash svg{
        transform: scale(4) translate(2.6rem, 2rem);
        }
  }