
main {
    display: flex;
    flex-direction: column;
    /* height: 100vh; */
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100%;
    min-height: 100vh;
  }
  
 main h1{
    font-size: 3em;
    padding-top: 100px;
    text-align: center;
    color: var(--orange);
    width: 100vw;
    animation: fade-down 1000ms ease-in-out forwards;
    
  }

  h1 span {
    color: var(--blue);
  }
  #home-splash{
    position: absolute;
    width: 200px;
    height: 200px;
    z-index: -1;
    background-image: url("../../designassets/fullmark.png");
    background-repeat: no-repeat;
    opacity: 0;
    transform: translateY(100px);
    animation: splash-fade 2000ms ease-in-out 1.1s forwards;
  }
  
  #home-text-container{
    padding: 10px;
    width: 80%;
    font-size: 1.4em;
    margin-top: 80px;
    margin-left: 20px;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.2);
    transform: rotateZ(-10deg);
    color: var(--blue);
    animation: spin 1000ms 500ms ease-out forwards;
    opacity: 0;
  }
  
  #home-text-container h2 {
    font-weight: bolder;
    color: var(--orange);
  }
  #home-text-container h4 {
    font-weight: lighter;
  }
  
  #apply-button-container{
    width: fit-content;
    height: fit-content;
    margin: 0 auto;
  }
  
  #home-apply-button {
    cursor: pointer;
    outline: none;
    padding: 15px;
    border-radius: 10px;
    font-size: 1.7em;
    color: var(--blue);
    border: 3px solid var(--orange);
    font-weight: bolder;
    background-color: transparent;
    margin-top: 60px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
    opacity: 0;
    animation: pop 500ms 1.6s linear forwards;
  }

  #home-apply-button:hover {
    border: 3px solid var(--blue);
    color: var(--orange);
  }

  .graphic {
 
    margin-top: -350px;
    background-image: url("../../designassets/graphic.png");
    background-repeat: no-repeat;
    background-size: 100vw 500px;
    height: 500px;
    opacity: 0;
    z-index: -1;
    animation: graphic-fade 2000ms ease-in-out 1.1s forwards;
  }

  @keyframes spin {
    from {
      transform: scale(0) rotateZ(0deg);
      opacity: 0;
    }

    to {
      transform: scale(1) rotateZ(-370deg);
      opacity: 1;
    }
  }

  @keyframes fade-down {
    from {
      transform: translateY(-20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes pop {
    from{
      opacity: 0;
    }
    to {
      opacity: 1;
    }
    
  }

  @keyframes splash-fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes graphic-fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 0.1;
    }
  }
  
  @media (min-width:500px){
    #home-splash{
      transform: scale(2) translateY(4rem);
    }
    #home-text-container{
      
      font-size: 2em;
    }

    #home-apply-button {
      transform: scale(1.5);
    }
  }
  @media (min-width:700px){

    .bigname {
      transform: scale(1.5);
      margin-top: 1rem;
    }
    #home-splash{
      transform: scale(2.5) translate(1rem, 7rem);
    }
    #home-text-container{
      font-size: 2.5em;
      margin-top: 120px;
    }

    #home-apply-button {
      transform: scale(1.5);
    }
  }
  @media (min-width:800px){

    #home-text-container{
      font-size: 3em;
      margin-top: 5rem;
    }
  }
  @media (min-width:1100px){

    #home-text-container{
      margin: 0 auto;
      width: 40rem;
      font-size: 3em;
      margin-top: 4rem;
    }

    .bigname {
      padding-top: 6rem;
      transform: scale(2);
    }

   
  }
  @media (min-width:1500px){
    main{
      overflow-y: auto;
      overflow: hidden;
    }
    
  }

  @media (min-height: 650px){
    .graphic {
      margin-top: -300px;
    }
  }


  @media (min-height:700px) {
   
  }

  @media (min-height: 800px) {
    .graphic {
      margin-top: -100px;
    }
  }

  @media (min-height: 900px) {
    .graphic {
      margin-top: 0px;
    }
  }

  @media (min-height: 1100px) {
    .graphic {
      margin-top: 200px;
    }
  }