
  .footer{
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    height: 70px;
    background: url("../../designassets/footer.png") no-repeat;
    background-size: 100% 70px;
    z-index: 10;
  }
  
  .footer div{
    margin-top: 10px;
    text-align: center;
  }
  
  .footer h5 {
    color: white;
  }
  
  
  .icon {
    font-size: 1.5em;
    color: var(--orange);
  }

  .navlink {
    color: white;
    text-decoration: none;
  }

  @media (min-width:1100px){
    .footer {
      display: none;
    }
    footer div{
      display: none;
      
    }
  }
  