:root{
  --blue: #102FA3;
  --orange: #F07C01;
}

#root{
  height: 100vh;
}

body {
  overflow-x: hidden;
  font-family: 'Montserrat', sans-serif;
  width: 100vw;
  /* height: 100vh; */
}


main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* height: 100vh; */
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100%;
    min-height: 100vh;
  }
  
 main h1{
    font-size: 3em;
    padding-top: 100px;
    text-align: center;
    color: var(--orange);
    width: 100vw;
    -webkit-animation: fade-down 1000ms ease-in-out forwards;
            animation: fade-down 1000ms ease-in-out forwards;
    
  }

  h1 span {
    color: var(--blue);
  }
  #home-splash{
    position: absolute;
    width: 200px;
    height: 200px;
    z-index: -1;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
    -webkit-animation: splash-fade 2000ms ease-in-out 1.1s forwards;
            animation: splash-fade 2000ms ease-in-out 1.1s forwards;
  }
  
  #home-text-container{
    padding: 10px;
    width: 80%;
    font-size: 1.4em;
    margin-top: 80px;
    margin-left: 20px;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.2);
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
    color: var(--blue);
    -webkit-animation: spin 1000ms 500ms ease-out forwards;
            animation: spin 1000ms 500ms ease-out forwards;
    opacity: 0;
  }
  
  #home-text-container h2 {
    font-weight: bolder;
    color: var(--orange);
  }
  #home-text-container h4 {
    font-weight: lighter;
  }
  
  #apply-button-container{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0 auto;
  }
  
  #home-apply-button {
    cursor: pointer;
    outline: none;
    padding: 15px;
    border-radius: 10px;
    font-size: 1.7em;
    color: var(--blue);
    border: 3px solid var(--orange);
    font-weight: bolder;
    background-color: transparent;
    margin-top: 60px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
    opacity: 0;
    -webkit-animation: pop 500ms 1.6s linear forwards;
            animation: pop 500ms 1.6s linear forwards;
  }

  #home-apply-button:hover {
    border: 3px solid var(--blue);
    color: var(--orange);
  }

  .graphic {
 
    margin-top: -350px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 100vw 500px;
    height: 500px;
    opacity: 0;
    z-index: -1;
    -webkit-animation: graphic-fade 2000ms ease-in-out 1.1s forwards;
            animation: graphic-fade 2000ms ease-in-out 1.1s forwards;
  }

  @-webkit-keyframes spin {
    from {
      -webkit-transform: scale(0) rotateZ(0deg);
              transform: scale(0) rotateZ(0deg);
      opacity: 0;
    }

    to {
      -webkit-transform: scale(1) rotateZ(-370deg);
              transform: scale(1) rotateZ(-370deg);
      opacity: 1;
    }
  }

  @keyframes spin {
    from {
      -webkit-transform: scale(0) rotateZ(0deg);
              transform: scale(0) rotateZ(0deg);
      opacity: 0;
    }

    to {
      -webkit-transform: scale(1) rotateZ(-370deg);
              transform: scale(1) rotateZ(-370deg);
      opacity: 1;
    }
  }

  @-webkit-keyframes fade-down {
    from {
      -webkit-transform: translateY(-20px);
              transform: translateY(-20px);
      opacity: 0;
    }
    to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes fade-down {
    from {
      -webkit-transform: translateY(-20px);
              transform: translateY(-20px);
      opacity: 0;
    }
    to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
  }

  @-webkit-keyframes pop {
    from{
      opacity: 0;
    }
    to {
      opacity: 1;
    }
    
  }

  @keyframes pop {
    from{
      opacity: 0;
    }
    to {
      opacity: 1;
    }
    
  }

  @-webkit-keyframes splash-fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes splash-fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @-webkit-keyframes graphic-fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 0.1;
    }
  }

  @keyframes graphic-fade {
    from {
      opacity: 0;
    }
    to {
      opacity: 0.1;
    }
  }
  
  @media (min-width:500px){
    #home-splash{
      -webkit-transform: scale(2) translateY(4rem);
              transform: scale(2) translateY(4rem);
    }
    #home-text-container{
      
      font-size: 2em;
    }

    #home-apply-button {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  @media (min-width:700px){

    .bigname {
      -webkit-transform: scale(1.5);
