              transform: scale(2.5) translate(1rem, 7rem);
    }
    #home-text-container{
      font-size: 2.5em;
      margin-top: 120px;
    }

    #home-apply-button {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  @media (min-width:800px){

    #home-text-container{
      font-size: 3em;
      margin-top: 5rem;
    }
  }
  @media (min-width:1100px){

    #home-text-container{
      margin: 0 auto;
      width: 40rem;
      font-size: 3em;
      margin-top: 4rem;
    }

    .bigname {
      padding-top: 6rem;
      -webkit-transform: scale(2);
              transform: scale(2);
    }

   
  }
  @media (min-width:1500px){
    main{
      overflow-y: auto;
      overflow: hidden;
    }
    
  }

  @media (min-height: 650px){
    .graphic {
      margin-top: -300px;
    }
  }


  @media (min-height:700px) {
   
  }

  @media (min-height: 800px) {
    .graphic {
      margin-top: -100px;
    }
  }

  @media (min-height: 900px) {
    .graphic {
      margin-top: 0px;
    }
  }

  @media (min-height: 1100px) {
    .graphic {
      margin-top: 200px;
    }
  }
.about-container {
