header {
  top: 0;
    position: fixed; 
    width: 100vw;
    height: 100px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100px;
    z-index: 10;
 
  }
  
  .upper-header-container {
   padding: 10px;
    width: calc(100vw - 20px);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
  }
  header .logo-container {
    position: relative;
  }
  
  .header-navlink{
    color: white;
    list-style-type: none;
    text-decoration: none;
    font-size: 2em;
    display: none;
  }

  .menu-icon {
    cursor: pointer;
    font-size: 2em;
    color: var(--orange);
    padding-right: 20px;
  }

 
  
  .name {
    text-align: center;
    font-size: 2.5em;
    padding-top: 10px;
    display: none;
  }
  
  .name span:nth-child(1){
    color: var(--orange);
  }
  
  .name span:nth-child(2){
    color: white;
  }
  
   

  .open {
    z-index: 2;
    top: 0;
    position: absolute;
    right: 0;
    transition:  500ms ease-in-out;
    width: 60%;
      height: 100vh;
      background-color: var(--blue);
      color: white;
      overflow-y: auto;
  }
  .closed {
    z-index: 3;
    position: absolute;
    right: -100%;
    top: 0;
    transition:  500ms ease-in-out;
    width: 60%;
      height: 100vh;
      background-color: var(--blue);
      color: white;
   
  }



  

  nav ul{
    margin: 0;
    padding: 0;
