    font-weight: bold;
}

.art-container li {
    margin: 60px 0;
}

.art-pieces {
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(50px, 500px));
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
    padding: 1rem;
    -webkit-justify-content: center;
            justify-content: center;
}

.art-pieces > div {
    width: 300px;
    height: 300px;
    justify-self: center;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0;
    transition: all 500ms ease-in-out forwards;
}

.art-pieces .overlay:hover{
    opacity: 0.5;
    -webkit-animation: overlay 500ms ease-in-out forwards;
            animation: overlay 500ms ease-in-out forwards;
}

.art-pieces h3 {
    opacity: 0;
    font-size: 2em;
    /* margin-top: 30px; */
    color: white;
    pointer-events: none;
    transition: all 500ms ease-in-out forwards;
}

.art-pieces p {
    font-size: 1em;
    margin: 20 auto;
    /* margin-top: 20%; */
    width: 80%;
    text-align: center;
    display: block;
    opacity: 0;
    color: white;
    pointer-events: none;
    transition: all 500ms ease-in-out forwards;
}

.art-pieces div:hover p {
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
