.art-container {
    min-height: 100vh;
    height: fit-content;
    padding-top: 6rem;
    width: 100%;
    perspective: 1000px;
}

.art-container h1 {
    color: var(--orange);
    text-align: center;
    font-size: 2.2em;
}

.art-container h3 {
    color: var(--blue);
    text-align: center;
    font-size: 1em;
}

.art-container h2 {
   margin: 10px 0;
}

.art-container ol {
    color: var(--orange);
    text-align: center;
    margin: 40px auto;
    width: 80%;
    padding-bottom: 5rem;
}

.art-container p {
    text-align: left;
    color: var(--blue);
    margin: 10px auto;
    width: 80%;
    font-size: 1.1em;
    font-weight: bold;
}

.art-container li {
    margin: 60px 0;
}

.art-pieces {
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(50px, 500px));
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
    padding: 1rem;
    justify-content: center;
}

.art-pieces > div {
    width: 300px;
    height: 300px;
    justify-self: center;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0;
    transition: all 500ms ease-in-out forwards;
}

.art-pieces .overlay:hover{
    opacity: 0.5;
    animation: overlay 500ms ease-in-out forwards;
}

.art-pieces h3 {
    opacity: 0;
    font-size: 2em;
    /* margin-top: 30px; */
    color: white;
    pointer-events: none;
    transition: all 500ms ease-in-out forwards;
}

.art-pieces p {
    font-size: 1em;
    margin: 20 auto;
    /* margin-top: 20%; */
    width: 80%;
    text-align: center;
    display: block;
    opacity: 0;
    color: white;
    pointer-events: none;
    transition: all 500ms ease-in-out forwards;
}

.art-pieces div:hover p {
    filter: brightness(200%);
    /* transform: rotateX(180deg); */
   animation: text 500ms 800ms ease-in-out forwards;
}
.art-pieces div:hover h3 {
    filter: brightness(200%);
    /* transform: rotateX(180deg); */
    animation: text 500ms 800ms ease-in-out forwards;
}



.art-pieces > div:nth-child(1){
    background-image: url("../../designassets/art1.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.art-pieces > div:nth-child(2){
    background-image: url("../../designassets/art2.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.art-pieces > div:nth-child(3){
    background-image: url("../../designassets/art3.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.art-pieces > div:nth-child(4){
    background-image: url("../../designassets/art4.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.art-pieces > div:nth-child(5){
    background-image: url("../../designassets/art5.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}
.art-pieces > div:nth-child(6){
    background-image: url("../../designassets/art6.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.art-pieces {
    transform-style: preserve-3d;
}

.art-pieces > div:hover {
   animation: img 800ms ease-in-out forwards;
}
@keyframes overlay {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.8;
    }
}

@keyframes text {
    from {
        opacity: 0;
        transform: translateY(0) rotateY(180deg);
    }
    to {
        opacity: 1;
        transform: translateY(30px) rotateY(180deg);
    }
}

@keyframes img {
    0% {
        background-size: 100% 100%;
        transform: rotateY(0);
    }

    100% {
        background-size: 120% 120%;
        transform: rotateY(-180deg);
    }
}




@media (min-width:600px){
    .art-container {
        font-size: 1.5em;
    }

    .art-pieces div {
        height: 500px;
        width: 500px;
    }
}

@media (min-width:1100px){
    .art-container {
        font-size: 1.5em;
        padding-top: 9rem;
    }
    .art-pieces div {
        font-size: 1.5em;
        width: 500px;
        height: 600px;
        justify-self: center;
    }
    
}

@media (min-width:1600px){
    .art-container {
        font-size: 2em;
        padding-top: 7rem;
    }
    
    .art-container p {
        font-size: 0.6em;
    }

    .art-container ol {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
    }

}
