.reviews-container {
    min-height: 100vh;
    height: fit-content;
    padding-top: 6rem;
    width: 100%;
    background-image: url("../../designassets/paint2.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.reviews-container h1{
    color: var(--orange);
    color: white;
    text-align: center;
}

.reviews-container figure {
    font-size: 1.1em;
    padding: 20px;
    margin: 20px auto;
    width: 80%;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(30px);
    border: 3px solid transparent;
    background-clip: padding-box;
    border-radius: 20px;
    box-shadow: 10px 10px 10px rgba(50, 50, 50, 0.6);
}

.reviews-container figcaption {
    color: var(--orange);
    text-align: right;
}

.reviews-container blockquote {
    color: var(--blue);
    color: white;
    margin: 10px 0;
    font-weight: bold;
}

.reviews-container span {
    color: var(--orange);
}

@media (min-width: 600px) {
    .reviews-container {
        font-size: 1.2em;
    }
}

@media (min-width: 1100px) {
    .reviews-container {
        font-size: 1.8em;
        padding-top: 8rem;
    }
    .review-wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}
